import * as THREE from 'three';

export default class Paths {
    constructor(_option) {
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.numDrawing = 10;
        this.pathWidth = 1.5;
        this.pathLength = 10;
        this.stairLength = 10;
        this.floorHeight = 3;
        this.level = 0;
        this.maxDistance = 0;
        this.paths = [];
        this.progress = [];

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('paths');
            this.debugFolder.open();
        }

        this.setPath();
        this.setDrawPath();
    }

    setPath() {
        const maxHeight = this.floorHeight * this.numDrawing;
        const origin = new THREE.Vector3(-this.stairLength / 2, - maxHeight / 2, this.pathLength / 2);

        this.paths.push(origin);
        this.progress.push(0);

        for (let i = 0; i < this.numDrawing; i += 1) {
            const sign = (i % 2) ? 1 : -1;

            const pointA = new THREE.Vector3((this.stairLength / 2) * sign, this.floorHeight * i - maxHeight / 2, (this.pathLength / 2) * sign);
            const pointB = new THREE.Vector3((this.stairLength / 2) * (-sign), this.floorHeight * (i + 1) - maxHeight / 2, (this.pathLength / 2) * sign);

            this.paths.push(pointA);
            this.paths.push(pointB);

            const distanceA = pointA.distanceTo(this.paths[2 * i]);
            const distanceB = pointB.distanceTo(this.paths[2 * i + 1]);

            this.progress.push(distanceA + this.progress[2 * i]);
            this.progress.push(distanceB + this.progress[2 * i + 1]);
        }

        this.maxDistance = this.progress[this.progress.length - 1];

        this.progress.forEach((currentDistance, i) => this.progress[i] = currentDistance / this.maxDistance);

        if (this.debug) {
            console.log('paths');
        }
    }

    setDrawPath() {
        const material = new THREE.LineBasicMaterial({ color: 'white' });
        const geometry = new THREE.BufferGeometry().setFromPoints(this.paths);
        const lines = new THREE.Line(geometry, material);

        this.container.add(lines);
    }

    setPosition(_progress) {
        if (_progress > this.progress[this.level + 1]) this.level += 1;
        if (_progress < this.progress[this.level]) this.level -= 1;

        const pointA = this.paths[this.level].clone();
        const pointB = this.paths[this.level + 1].clone();
        const progressA = this.progress[this.level];
        const progressB = this.progress[this.level + 1];

        const factor = (_progress - progressB) / (progressA - progressB);

        const vectorA = pointA.multiplyScalar(factor);
        const vectorB = pointB.multiplyScalar(1 - factor);
        const position = vectorA.add(vectorB);

        return position;
    }
}
